@import url("../../common/styles/common.css");


.profileDescription__box{
    position: absolute;
    width: 755px;
    height: 787px;
    left: 378px;
    top: 71px;
    background: #FCFCFC;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.profileDescription__header{
    font-family: 'RobotoFlex', serif;
    font-style: normal;
    font-weight: 400;
    position: absolute;
    top: 38%;
    font-size: 22px;
}

.profileDescription__profileImg{
    position: absolute;
    width: 148px;
    height: 148px;
    left: calc(50% - 148px/2);
    top: 20%;
    box-shadow: inset 0px 2px 4px rgba(0, 0, 0, 0.1);
    border-radius: 100px;
}
.profileDescription__arrowImg{
    width: 32px;
    height: 35px;
}

.profileDescription__arrowLink{
    align-self: flex-start;
}
.profileDescription__inputWrapper{
    margin:5px;
}


.profileDescription__formData{
    position: absolute;
    top: 45%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.profileDescription__input{
    width: 500px;
    height: 40px;
    outline: none;
    border: none;
    border-bottom: 1px solid #000000;
}
.profileDescription__input:disabled{
    background: #FCFCFC;
}

.profileDescription__inputLabel{
    display: inline-block;
    font-family: 'RobotoFlex', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    text-align: right;
}

.profileDescription__button{
    position: static;
    left: 0;
    margin-top: 20px;
}

.profileDescription__linkBox{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    left: 10%;
    bottom: 10%;
}